<template>
  <div class="companyInfo"> 
    <div class="detailInfo">
      <div class="container">
        <div class="jobDescrit">
          <p class="title">职位描述</p>
          <p class="normalTitle">岗位职责</p>
          <ul>
            <li>{{data.postDesc}}</li>
          </ul>
          <p  class="normalTitle">任职要求：</p>
          <ul>
            <li>{{data.postDesc}}</li>
          </ul>
        </div>
        <!-- <div class="busInfo">
          <p class="title">工商信息</p>
          <div>
            <span>法人代表：黄大伟</span>
            <span>注册资本：3750万元</span>
            <span>成立时间：2013-11-18</span>
          </div>
        </div> -->
        <div class="companyAddress">
          <p class="title">公司地址</p>
          <div class="map" id="map">

          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name:'CompanyInfo',
  props:{
    data:{
      type:Object,
      default:()=>{}
    },
    com:{
      type:Object,
      default:()=>{}
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init(){
      // 获取到作为地图容器的DOM元素
      const container = document.querySelector('#map')
      // 创建地图实例
      const map = new AMap.Map(container,{
        resizeEnable: true
      })
      const geocoder = new AMap.Geocoder({
        city: "010", //城市设为北京，默认：“全国”
      });
      // 往地图里加入更多元素（如点标记）
      const marker = new AMap.Marker({
        position:[this.com.comLat,this.com.comLong]
      })
    }
  },
}
</script>
<style lang="less">
.companyInfo{
  .detailInfo{
    text-align: left;
    .jobDescrit{
      margin: 10px;
      .title{
        color: #10BBB8;
        padding: 10px 0;
        border-bottom: 1px solid #81d3f8;
      }
      .normalTitle{
        margin: 10px 0;
      }
    }
    .busInfo{
      margin: 10px;
      .title{
        color: #10BBB8;
        padding: 10px 0;
        border-bottom: 1px solid #81d3f8;
      }
    }
    .companyAddress{
      margin: 10px;
      .title{
        color: #10BBB8;
        padding: 10px 0;
        border-bottom: 1px solid #81d3f8;
      }
      .map{
        margin: 20px 0;
        height: 400px;
      }
    }
  }
}
</style>


