<template>
  <div class="jobList">
    <ul>
    <li class="listLi" v-for="(item,index) in list" :key="index">
      <div class="jobDescrit">
        <p class="jobName"><router-link :to="{path:'jobDetail',query:{id:item.id}}">{{item.positionName}}  [{{item.workCity}}·{{item.workAdr}}]</router-link></p>
        <p class="jobDep"><span>{{item.salaryBegin}}K~{{item.salaryEnd}}K</span>|<span>{{getExpReq(item.expReq)}}</span>|<span>{{getEduReq(item.eduReq)}}</span></p>
        <p class="jobTag">
          <span class="time">2小时前</span>
          <span class="tag">{{item.postKey}}</span>
        </p>
      </div>
      <div class="line"></div>
      <div class="companyDescrit">
        <p class="companyName"><router-link :to="{path:'companyDetail',query:{id:item.id}}" >{{item.comName}}</router-link></p>
        <p class="companyDep"><span>{{item.postDesc}}</span>|<span>不需要融资</span>|<span>0-20人</span></p>
        <p class="jobTag">
          <span class="tag">{{item.postKey}}</span>
        </p>
      </div>
    </li>
    </ul>
    <div class="page">
      <el-pagination
        :current-page="currentPage"
        :page-sizes="[10, 50, 100, 200]"
        :page-size="10"
        layout="prev, pager, next, sizes, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  name:'jobList',
  data(){
    return {
      currentPage:1
    }
  },
  props:{
    list:{
      type:Array,
      default:()=>[]
    },
    total:{
      type:Number,
      default:0
    }
  },
  methods:{
    getExpReq(val){
      let newVal
      switch(val){
        case 0:
        newVal = '不限'
        break;
        case 1:
        newVal = '1-3年'
        break;
        case 2:
        newVal = '3-5年'
        break;
        case 3:
        newVal = '5-10年'
        break;
        case 4:
        newVal = '10年以上'
        break;
        default:
        break;
      }
      return newVal;
    },
    getEduReq(val){
      let newVal
      switch(val){
        case 0:
        newVal = '不限'
        break;
        case 1:
        newVal = '中专及以上'
        break;
        case 2:
        newVal = '大专及以上'
        break;
        case 3:
        newVal = '本科及以上'
        break;
        case 4:
        newVal = '硕士及以上'
        break;
        case 5:
        newVal = '博士及以上'
        break;
        default:
        break;
      }
      return newVal;
    },
  }
}
</script>
<style lang="less">
    .jobList{
      min-height: 300px;
      float: left;
      width: 824px;
      display: inline-block;
      overflow: hidden;
      .listLi{
        background-color: #fff;
        margin-top: 10px;
        padding:10px;
        overflow: hidden;
        border:1px solid #10bbb8;
        .jobDescrit{
          width: 360px;
          float: left;
          p{
            margin-top:20px;
            &.jobName a{
              color:#666;
              &:hover{cursor: pointer;}
            }
            &.jobDep{
              span{
                margin: 0 20px;
                &:nth-child(1){
                  color:#F45C2C;
                  margin-left: 0;
                }
              }
            }
          }
        }
        .line{
          float: left;
          width: 1px;
          margin: 0 20px;
          background-color:#10bbb8;
          height: 100%;
          min-height: 140px;
        }
        .companyDescrit{
          
          width: 360px;
          float: left;
          p{
            margin-top:20px;
            &.companyName a{
              color:#10bbb8
            }
            &.companyDep{
              span{
                margin: 0 20px;
                &:nth-child(1){
                  margin-left: 0;
                }
              }
            }
          }
        }
        .time{
          color:#666;
          font-size:12px;
        }
        .tag{
          font-size: 12px;
          display: inline-block;
          padding: 8px 3px;
          border-radius:5px;
          margin-bottom: 10px;
          background-color: #edf9ff;
          color:#4B9FCF;
        }
      }
    }
    .page{
      text-align: right;
      margin: 20px 0;
    } 
    
  .el-pagination .btn-prev,.el-pagination .btn-next{
    padding: 0 6px;
    border-radius: 2px;
    border:1px solid #DCDFE6; 
  }
  .el-pagination li{
    color:#10bbb8;
    font-weight: normal;
    border:1px solid #DCDFE6;
    margin: 0 5px;
    border-radius: 4px;
  }
  .el-pager li.active+li{
    border:1px solid #DCDFE6;
  }
  .el-pager li.active{
    background-color:#10bbb8;
    color:#fff;
  }
  .el-pagination__editor.el-input .el-input__inner{
    color: #10bbb8;
  }
</style>


