<template>
<p class="bread">
  <span>您的当前位置：</span>
  <el-breadcrumb separator="-">
    <el-breadcrumb-item :to="(item.url==='')?'':({ path:item.url })" v-for="(item,index) in data" :key="index">{{item.name}}</el-breadcrumb-item>
  </el-breadcrumb>
</p>
</template>
<script>
export default {
  name:'bread',
  props:{
    data:{
      type:Array,
      default:()=>[{name:'首页',url:'/'}]
    }
  }
}
</script>
<style lang="less">

  .bread{
    text-align: left;
  }
</style>


