<template>
        <div class="other">
          <div class="viewed">
            <p class="title">看过的职位</p>
            <ul>
              <li v-for="(item,index) in list">
                <p><span class="job">{{item.positionName}}</span><span class="money">{{item.salaryBegin}}K~{{item.salaryEnd}}K</span></p>
                <p class="company">{{item.comName}}</p>
              </li>
            </ul>
          </div>
          <div class="searchWith">
            <p class="title">相关搜索</p>
            <ul>
              <li  v-for="(item,index) in list"><p><span class="job">{{item.industry}}</span><span class="more">>></span></p></li>
            </ul>
          </div>
        </div>
</template>
<script>
export default {
  props:{
    list:{
      type:Array,
      default:()=>[]
    }
  }
  
}
</script>
<style lang="less">

  .other{
    float: right;
    margin-bottom: 20px;
    .viewed{
      margin-top:20px;
      width: 257px;
      padding: 0 10px;
      overflow: hidden;
      border-radius: 5px;
      background-color: #fff;
      .title{
        color: #10BBB8;
        padding: 10px 0;
        border-bottom: 1px solid #81d3f8;
      }
      ul{
        overflow: hidden;
        li{
          padding: 10px 0;
          border-bottom: 1px solid #d0e7f1;
          &:nth-last-child(1){
            border-bottom: 0 none;
          }
          p{
            span{
              height: 18px;
              line-height: 18px;
            }
            .job{
              &:hover{
                cursor: pointer;
              }
              color:#333333
            }
            .money{
              float:right;
              color:#F45C2C
            }
            color:#7F7F7F
          }
        }
      }
    }
    .searchWith{
      margin-top:20px;
      width: 257px;
      padding: 0 10px;
      overflow: hidden;
      border-radius: 5px;
      background-color: #fff;
      .title{
        color: #10BBB8;
        padding: 10px 0;
        border-bottom: 1px solid #81d3f8;
      }
      ul{
        overflow: hidden;
        li{
          padding: 10px 0;
          border-bottom: 1px solid #d0e7f1;
          &:nth-last-child(1){
            border-bottom: 0 none;
          }
          p{
            span{
              height: 18px;
              line-height: 18px;
            }
            .job{
              color:#333333
            }
            .more{
              float:right;
              color:#7F7F7F;
              &:hover{
                cursor: pointer;
              }
            }
          }
        }
      }
    }
  }
</style>


